        <text class="back-icon">‚Üê</text>
      </view>
      <text class="nav-title">ÂéÜÂ∞èÂåÖAIÂä©Êâã</text>
      <view class="nav-right">
      </view>
    </view>
    
    <!-- Ê∏êÂèòËÉåÊôØË£ÖÈ•∞ -->
    <view class="bg-decoration">
      <view class="circle circle-1"></view>
      <view class="circle circle-2"></view>
      <view class="circle circle-3"></view>
    </view>
	
    <!-- ËÅäÂ§©ÁïåÈù¢ -->
    <view class="chat-container">
      
      <!-- Ê∂àÊÅØÂàóË°® -->
      <scroll-view class="message-list" scroll-y :scroll-top="scrollTop">
        <!-- Ê¨¢ËøéÊ∂àÊÅØ -->
        <view class="message-item assistant" v-if="messages.length === 0">
          <view class="message-avatar">
            <image class="avatar-image" src="https://llzm-lixiaobao.oss-cn-beijing.aliyuncs.com/logo.jpg" mode="aspectFill" />
          </view>
          <view class="message-content">
            <text class="message-text">‰Ω†Â•ΩÔºåÊúâ‰ªÄ‰πàÊàëËÉΩÂ∏Æ‰Ω†ÁöÑÂêó</text>
          </view>
        </view>
        
        <!-- ÂØπËØùÊ∂àÊÅØ -->
        <view class="message-item" v-for="(message, index) in messages" :key="index" :class="message.role">
          <view class="message-avatar">
            <image v-if="message.role === 'assistant'" class="avatar-image" src="https://llzm-lixiaobao.oss-cn-beijing.aliyuncs.com/logo.jpg" mode="aspectFill" />
            <view v-else class="avatar-text">üë§</view>
          </view>
          <view class="message-content">
            <rich-text class="message-text" :nodes="formatMessage(message.content)"></rich-text>
          </view>
        </view>
        
        <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
        <view class="message-item assistant" v-if="isLoading">
          <view class="message-avatar">
            <image class="avatar-image" src="https://llzm-lixiaobao.oss-cn-beijing.aliyuncs.com/logo.jpg" mode="aspectFill" />
          </view>
          <view class="message-content">
            <view class="typing-indicator">
              <view class="typing-dot"></view>
              <view class="typing-dot"></view>
              <view class="typing-dot"></view>
            </view>
          </view>
        </view>
      </scroll-view>
     
      <!-- ËæìÂÖ•Âå∫Âüü -->
      <view class="input-area">
        <view class="input-wrapper">
          <input 
            class="message-input" 
            v-model="inputMessage" 
            placeholder="ËæìÂÖ•‰Ω†ÁöÑÈóÆÈ¢ò"
            :disabled="isLoading"
            @confirm="sendMessage"
          />
          <button class="send-button" @click="sendMessage" :class="{ 
            disabled: !inputMessage.trim() || isLoading,
            sending: isLoading 
          }">
            <text v-if="!isLoading">ÂèëÈÄÅ</text>
            <text v-else>ÂèëÈÄÅ‰∏≠...</text>
          </button>
        </view>
      </view>
    </view>
    
    <!-- ÂÖ®Â±ÄÊÇ¨ÊµÆÂà∑Êñ∞ÊåâÈíÆÔºàÂèØÊãñÂä®Ôºâ -->
    <view 
      class="global-refresh-btn" 
      @click="handleRefresh"
      @touchstart="handleTouchStart"
      @touchmove="handleTouchMove"
      @touchend="handleTouchEnd"
      :style="{ 
        left: btnX + 'px', 
        bottom: btnY + 'px' 
      }"
    >
      <image class="refresh-icon-img" src="https://llzm-lixiaobao.oss-cn-beijing.aliyuncs.com/%E5%88%B7%E6%96%B0.png" mode="widthFix"></image>
    </view>
  </view>
</template>

<script>
import config from '@/utils/config.js';
import careModeMixin from '@/mixins/careMode.js';

export default {
  mixins: [careModeMixin],
  data() {
    return {
      statusBarHeight: 0,
      inputMessage: '',
      messages: [],
      isLoading: false,
      scrollTop: 0,
      faqList: [
        {
          question: '‰ªÄ‰πàÊòØ"Ê¥ûËßÅÂéÜÂè≤"Á†îÂ≠¶È°πÁõÆÔºü',
          answer: '"Ê¥ûËßÅÂéÜÂè≤"ÊòØ‰∏Ä‰∏™Ë∑®Â≠¶ÁßëÈ°πÁõÆÂºèÂ≠¶‰π†È°πÁõÆÔºåÊó®Âú®ÈÄöËøáÂéÜÂè≤ÊñáÂåñÁöÑÊ∑±Â∫¶Êé¢Á¥¢ÔºåÂüπÂÖªÂ≠¶ÁîüÁöÑÊâπÂà§ÊÄßÊÄùÁª¥ÂíåÂàõÊñ∞ËÉΩÂäõ„ÄÇ'
        },
        {
          question: 'ÊïôËÇ≤4.0Êó∂‰ª£Êúâ‰ªÄ‰πàÁâπÁÇπÔºü',
          answer: 'ÊïôËÇ≤4.0Êó∂‰ª£Âº∫Ë∞É‰∏™ÊÄßÂåñÂ≠¶‰π†„ÄÅË∑®Â≠¶ÁßëÊï¥Âêà„ÄÅÊäÄÊúØËûçÂêàÂíåÁªàË∫´Â≠¶‰π†ÔºåÊ≥®ÈáçÂüπÂÖªÂ≠¶ÁîüÁöÑÂàõÊñ∞ÊÄùÁª¥ÂíåÂÆûË∑µËÉΩÂäõ„ÄÇ'
        },
        {
          question: 'Â¶Ç‰ΩïÂèÇ‰∏éÈ°πÁõÆÂ≠¶‰π†Ôºü',
          answer: 'ÊÇ®ÂèØ‰ª•ÈÄöËøáÂ∞èÁ®ãÂ∫èÊµèËßàÈ°πÁõÆÂÜÖÂÆπÔºåÂèÇ‰∏éÂú®Á∫øËÆ®ËÆ∫ÔºåÊèê‰∫§Â≠¶‰π†ÊàêÊûúÔºåÂπ∂‰∏éÂõ¢ÈòüÊàêÂëòÂçè‰ΩúÂÆåÊàêÈ°πÁõÆ‰ªªÂä°„ÄÇ'
        },
        {
          question: 'È°πÁõÆÊúâÂì™‰∫õÂ≠¶‰π†ËµÑÊ∫êÔºü',
          answer: 'È°πÁõÆÊèê‰æõ‰∏∞ÂØåÁöÑÂ≠¶‰π†ËµÑÊ∫êÔºåÂåÖÊã¨ÂéÜÂè≤ÊñáÁåÆ„ÄÅÂ§öÂ™í‰ΩìËµÑÊñô„ÄÅ‰∏ìÂÆ∂ËÆ≤Â∫ß„ÄÅÂÆûË∑µÊ°à‰æãÁ≠âÔºåÊîØÊåÅÂ§öÁßçÂ≠¶‰π†ÊñπÂºè„ÄÇ'
        }
      ],
      // ÊãñÂä®Áõ∏ÂÖ≥Áä∂ÊÄÅ
      btnX: 15,          
      btnY: 250, 
      startX: 0,         
      startY: 0,   
      windowWidth: 375, 
      windowHeight: 667, 
      isDragging: false 
    }
  },
  onLoad() {
    const sys = uni.getSystemInfoSync();
    this.statusBarHeight = sys.statusBarHeight || 0;
    this.loadFaqList();
    
    // ÂàùÂßãÂåñÁ™óÂè£ÂÆΩÈ´òÂíåÊåâÈíÆ‰ΩçÁΩÆ
    this.windowWidth = sys.windowWidth;
    this.windowHeight = sys.windowHeight;
    this.btnX = 30 / 2; // ËΩ¨Êç¢rpx‰∏∫px
    this.btnY = 500 / 2;
  },
  methods: {
    // Ê†ºÂºèÂåñÊ∂àÊÅØÂÜÖÂÆπÔºåÂ§ÑÁêÜMarkdown
    formatMessage(content) {
      if (!content) return '';
      
      // Ê∏ÖÁêÜÂíåÊ†ºÂºèÂåñÂÜÖÂÆπ
      let formatted = content
        // Â§ÑÁêÜÊ†áÈ¢ò - ÁßªÈô§#Á¨¶Âè∑Âπ∂ËΩ¨Êç¢‰∏∫Ê†∑Âºè
        .replace(/^#{1,6}\s+(.*)$/gm, (match, title) => {
          const level = match.match(/^#+/)[0].length;
          const fontSize = Math.max(28, 40 - level * 4);
          return `<div style="font-size: ${fontSize}rpx; font-weight: bold; margin: 20rpx 0 10rpx 0; color: #333; line-height: 1.4;">${title}</div>`;
        })
        // Â§ÑÁêÜÁ≤ó‰Ωì
        .replace(/\*\*(.*?)\*\*/g, '<span style="font-weight: bold;">$1</span>')
        // Â§ÑÁêÜÊñú‰Ωì
        .replace(/\*(.*?)\*/g, '<span style="font-style: italic;">$1</span>')
        // Â§ÑÁêÜÂàóË°®È°π
        .replace(/^[\s]*[-*+]\s+(.*)$/gm, '<div style="margin: 8rpx 0; padding-left: 20rpx; position: relative;"><span style="position: absolute; left: 0; color: #666;">‚Ä¢</span>$1</div>')
        // Â§ÑÁêÜÊï∞Â≠óÂàóË°®
        .replace(/^[\s]*(\d+)\.\s+(.*)$/gm, '<div style="margin: 8rpx 0; padding-left: 20rpx; position: relative;"><span style="position: absolute; left: 0; color: #666;">$1.</span>$2</div>')
        // Â§ÑÁêÜÊç¢Ë°å
        .replace(/\n/g, '<br/>')
        // Ê∏ÖÁêÜÂ§ö‰ΩôÁöÑ#Á¨¶Âè∑
        .replace(/#{2,}/g, '')
        // Ê∏ÖÁêÜË°åÈ¶ñÁöÑ#Á¨¶Âè∑
        .replace(/^#+\s*/gm, '');
      
      return formatted;
    },
    
    // ÂéüÂßãËøîÂõûÈÄªËæë‰øùÊåÅ‰∏çÂèò
    goBack() {
      console.log('ËøîÂõûÊåâÈíÆË¢´ÁÇπÂáª');
      uni.showToast({
        title: 'ËøîÂõûÊåâÈíÆË¢´ÁÇπÂáª',
        icon: 'none',
        duration: 1000
      });
      
      try {
        // ÂÖàÂ∞ùËØïËøîÂõû‰∏ä‰∏ÄÈ°µ
        const pages = getCurrentPages();
        console.log('ÂΩìÂâçÈ°µÈù¢Ê†àÈïøÂ∫¶:', pages.length);
        console.log('ÂΩìÂâçÈ°µÈù¢:', pages[pages.length - 1].route);
        
        if (pages.length > 1) {
          console.log('Â∞ùËØïËøîÂõû‰∏ä‰∏ÄÈ°µ');
          uni.navigateBack({
            delta: 1,
            animationType: 'slide-out-right',
            animationDuration: 300,
            success: () => {
              console.log('ËøîÂõûÊàêÂäü');
            },
            fail: (err) => {
              console.error('ËøîÂõûÂ§±Ë¥•:', err);
              // Â§áÁî®ÊñπÊ°àÔºöË∑≥ËΩ¨Âà∞È¶ñÈ°µ
              uni.switchTab({
                url: '/pages/index/index'
              });
            }
          });
        } else {
          console.log('Ê≤°Êúâ‰∏ä‰∏ÄÈ°µÔºåË∑≥ËΩ¨Âà∞È¶ñÈ°µ');
          // Â¶ÇÊûúÊ≤°Êúâ‰∏ä‰∏ÄÈ°µÔºåË∑≥ËΩ¨Âà∞È¶ñÈ°µ
          uni.switchTab({
            url: '/pages/index/index',
            success: () => {
              console.log('Ë∑≥ËΩ¨È¶ñÈ°µÊàêÂäü');
            },
            fail: (err) => {
              console.error('Ë∑≥ËΩ¨È¶ñÈ°µÂ§±Ë¥•:', err);
            }
          });
        }
      } catch (error) {
        console.error('ËøîÂõûÂ§±Ë¥•:', error);
        // Â§áÁî®ÊñπÊ°àÔºöË∑≥ËΩ¨Âà∞È¶ñÈ°µ
        uni.switchTab({
          url: '/pages/index/index'
        });
      }
    },
    
    // Ê∏ÖÁ©∫ÂØπËØù
    clearChat() {
      uni.showModal({
        title: 'Á°ÆËÆ§Ê∏ÖÁ©∫',
        content: 'Á°ÆÂÆöË¶ÅÊ∏ÖÁ©∫ÊâÄÊúâÂØπËØùËÆ∞ÂΩïÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            this.messages = [];
            this.scrollToBottom();
          }
        }
      });
    },
    
    // ÂèëÈÄÅÊ∂àÊÅØ
    async sendMessage() {
      if (!this.inputMessage.trim() || this.isLoading) return;
      
      const userMessage = this.inputMessage.trim();
      this.inputMessage = '';
      
      // Ê∑ªÂä†Áî®Êà∑Ê∂àÊÅØ
      this.messages.push({
        role: 'user',
        content: userMessage,
        timestamp: new Date()
      });
      
      this.scrollToBottom();
      this.isLoading = true;
      
      try {
        // ÊûÑÂª∫ÂØπËØùÂéÜÂè≤
        const conversationHistory = this.messages
          .filter(msg => msg.role !== 'system')
          .map(msg => ({
            role: msg.role,
            content: msg.content
          }));
        
        // Ë∞ÉÁî®AI API
        const response = await uni.request({
          url: `https://llzmlxb.xyz/api/ai/ask`,
          method: 'POST',
          data: {
            question: userMessage,
            conversationHistory: conversationHistory
          },
          timeout: 30000
        });
        
        if (response.data && response.data.success) {
          // Ê∑ªÂä†AIÂõûÂ§ç
          this.messages.push({
            role: 'assistant',
            content: response.data.data.answer,
            timestamp: new Date()
          });
        } else {
          throw new Error(response.data?.error || 'AIÊúçÂä°ÊöÇÊó∂‰∏çÂèØÁî®');
        }
        
      } catch (error) {
        console.error('AIÈóÆÁ≠îÂ§±Ë¥•:', error);
        this.messages.push({
          role: 'assistant',
          content: 'Êä±Ê≠âÔºåAIÊúçÂä°ÊöÇÊó∂‰∏çÂèØÁî®ÔºåËØ∑Á®çÂêéÂÜçËØï„ÄÇ',
          timestamp: new Date()
        });
      } finally {
        this.isLoading = false;
        this.scrollToBottom();
      }
    },
    
    // ÁÇπÂáªÂ∏∏ËßÅÈóÆÈ¢ò
    askQuestion(question) {
      this.inputMessage = question;
      this.sendMessage();
    },
    
    // Âä†ËΩΩÂ∏∏ËßÅÈóÆÈ¢òÂàóË°®
    async loadFaqList() {
      try {
        const response = await uni.request({
          url: `http://8.137.115.16:3000/api/ai/faq`,
          method: 'GET'
        });
        
        if (response.data && response.data.success) {
          this.faqList = response.data.data;
        }
      } catch (error) {
        console.error('Âä†ËΩΩÂ∏∏ËßÅÈóÆÈ¢òÂ§±Ë¥•:', error);
      }
    },
    
    // ÊªöÂä®Âà∞Â∫ïÈÉ®
    scrollToBottom() {
      this.$nextTick(() => {
        this.scrollTop = 99999;
      });
    },
    
    // Ê†ºÂºèÂåñÊó∂Èó¥
    formatTime(timestamp) {
      const date = new Date(timestamp);
      const now = new Date();
      const diff = now - date;
      
      if (diff < 60000) { // 1ÂàÜÈíüÂÜÖ
        return 'ÂàöÂàö';
      } else if (diff < 3600000) { // 1Â∞èÊó∂ÂÜÖ
        return Math.floor(diff / 60000) + 'ÂàÜÈíüÂâç';
      } else if (diff < 86400000) { // 1Â§©ÂÜÖ
        return Math.floor(diff / 3600000) + 'Â∞èÊó∂Ââç';
      } else {
        return date.toLocaleDateString();
      }
    },
    
    // ÂÖ®Â±ÄÂà∑Êñ∞ÊåâÈíÆÈÄªËæë
    async handleRefresh() {
      // Â¶ÇÊûúÂ§Ñ‰∫éÊãñÂä®Áä∂ÊÄÅÔºå‰∏çÊâßË°åÂà∑Êñ∞ÔºàÈÅøÂÖçËØØËß¶Ôºâ
      if (this.isDragging) return;

      // ÊòæÁ§∫Âä†ËΩΩÊèêÁ§∫
      uni.showLoading({
        title: 'Âà∑Êñ∞‰∏≠...',
        mask: true
      });
      
      try {
        // 1. Ê∏ÖÁ©∫Áé∞ÊúâÂØπËØù
        this.messages = [];
        // 2. ÈáçÊñ∞Âä†ËΩΩFAQÂàóË°®
        await this.loadFaqList();
        // 3. ÊªöÂä®Âà∞Â∫ïÈÉ®
        this.scrollToBottom();
      } catch (error) {
        console.error('Âà∑Êñ∞Â§±Ë¥•:', error);
        uni.showToast({
          title: 'Âà∑Êñ∞Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none',
          duration: 1500
        });
      } finally {
        // ÈöêËóèÂä†ËΩΩÊèêÁ§∫
        uni.hideLoading();
      }
    },
    
    // Ëß¶Êë∏ÂºÄÂßãÔºöËÆ∞ÂΩïÂàùÂßã‰ΩçÁΩÆ
    handleTouchStart(e) {
      const touch = e.touches[0];
      this.startX = touch.clientX;
      this.startY = touch.clientY;
      this.isDragging = false;
    },
    
    // Ëß¶Êë∏ÁßªÂä®ÔºöÊõ¥Êñ∞ÊåâÈíÆ‰ΩçÁΩÆ
    handleTouchMove(e) {
      const touch = e.touches[0];
      const currentX = touch.clientX;
      const currentY = touch.clientY;

      // ËÆ°ÁÆóÂÅèÁßªÈáè
      const deltaX = currentX - this.startX;
      const deltaY = currentY - this.startY;

      // Âà§Êñ≠ÊòØÂê¶Ëß¶ÂèëÊãñÂä®ÔºàÂÅèÁßªÈáèË∂ÖËøá5pxËßÜ‰∏∫ÊãñÂä®Ôºâ
      if (Math.abs(deltaX) > 5 || Math.abs(deltaY) > 5) {
        this.isDragging = true;

        // ËÆ°ÁÆóÊñ∞‰ΩçÁΩÆ
        let newBtnX = this.btnX + deltaX;
        let newBtnY = this.btnY - deltaY; // YËΩ¥ÂèçÂêë

        // ÊåâÈíÆÂ∞∫ÂØ∏ÔºàrpxËΩ¨Êç¢‰∏∫pxÔºâ
        const btnWidth = this.careMode ? 110 / 2 : 80 / 2;
        const btnHeight = this.careMode ? 110 / 2 : 80 / 2;
        
        // ÈôêÂà∂ËæπÁïå
        newBtnX = Math.max(10, Math.min(newBtnX, this.windowWidth - btnWidth - 10));
        newBtnY = Math.max(10, Math.min(newBtnY, this.windowHeight - btnHeight - 10));

        // Êõ¥Êñ∞‰ΩçÁΩÆ
        this.btnX = newBtnX;
        this.btnY = newBtnY;

        // Êõ¥Êñ∞Ëµ∑Âßã‰ΩçÁΩÆ
        this.startX = currentX;
        this.startY = currentY;
      }
    },
    
    // Ëß¶Êë∏ÁªìÊùüÔºöÊ∑ªÂä†ËæπÁºòÂê∏ÈôÑÊïàÊûú
    handleTouchEnd() {
      // ËæπÁºòÂê∏ÈôÑÊïàÊûú
      const halfWidth = this.windowWidth / 2;
      const btnWidth = this.careMode ? 110 / 2 : 80 / 2;
      
      if (this.btnX > halfWidth) {
        // Âê∏ÈôÑÂà∞Âè≥‰æß
        this.btnX = this.windowWidth - btnWidth - 10;
      } else {
        // Âê∏ÈôÑÂà∞Â∑¶‰æß
        this.btnX = 10;
      }
    }
  }
};
</script>

